@import "variables";

/* used in combination with layouts/_default/_markup/render-image.html */
img {
    max-width: 100%;
    height: auto;
}

img {
    /* center images */
    display: block;
    margin: auto;

    /* add border */
    border: 1px solid rgba(0, 0, 0, 0.2);
}

table th {
    position: sticky;
    top: 0;
}

table thead th {
    background-color: $alt-bg-color;
    color: $alt-fg-color;
}

body.colorscheme-dark table thead th {
    background-color: $alt-bg-color-dark;
    color: $alt-fg-color-dark;
}

.footnotes {
    font-size: 0.8em;
}

@import "github";

body.colorscheme-dark {
    @import "gruvbox";
}